	var util = require("../../utils/util.js"); //获取应用实例
	var weburl = getApp().globalData.weburl;
	var shop_type = getApp().globalData.shop_type;
	var username = wx.getStorageSync('username');
	var md5_key = getApp().globalData.md5_key;
    import md5 from '../../utils/md5.js';
	import service from '../../service.js';
    import {
        mapState,
        mapMutations
    } from 'vuex'
    import mInput from '../../components/m-input.vue'
	if(username){
		uni.navigateBack();
	}
	
    export default {
        components: {
            mInput
        },
        data() {
            return {
                providerList: [],
                hasProvider: false,
				bypassword:true,
                account: '',
                password: '',
                positionTop: 0,
				vcode:'',
				shop_type:shop_type,
            }
        },
        computed: mapState(['forcedLogin']),
        methods: {
            ...mapMutations(['login']),
            initProvider() {
                const filters = ['weixin', 'qq', 'sinaweibo'];
                uni.getProvider({
                    service: 'oauth',
                    success: (res) => {
                        if (res.provider && res.provider.length) {
                            for (let i = 0; i < res.provider.length; i++) {
                                if (~filters.indexOf(res.provider[i])) {
                                    this.providerList.push({
                                        value: res.provider[i],
                                        image: '/static/' + res.provider[i] + '.png'
                                    });
                                }
                            }
                            this.hasProvider = true;
                        }
                    },
                    fail: (err) => {
                        console.error('获取服务供应商失败：' + JSON.stringify(err));
                    }
                });
            },
            initPosition() {
                /**
                 * 使用 absolute 定位，并且设置 bottom 值进行定位。软键盘弹出时，底部会因为窗口变化而被顶上来。
                 * 反向使用 top 进行定位，可以避免此问题。
                 */
                this.positionTop = uni.getSystemInfoSync().windowHeight - 100;
            },
            bindLogin() {
                if (!util.checkPhoneNumber(this.account)) {
                    uni.showToast({
                        icon: 'none',
                        title: '手机号格式错误'
                    });
                    return;
                }
                if (this.password.length < 6 && this.bypassword) {
                    uni.showToast({
                        icon: 'none',
                        title: '密码最短为 6 个字符'
                    });
                    return;
                }
				if (!this.vcode && !this.bypassword) {
				    uni.showToast({
				        icon: 'none',
				        title: '验证码不能为空'
				    });
				    return;
				}
				this.my_login()
               /*
                const data = {
                    account: this.account,
                    password: this.password
                };
                const validUser = service.getUsers().some(function (user) {
                    return data.account === user.account && data.password === user.password;
                });
                if (validUser) {
                    this.toMain(this.account);
                } else {
                    uni.showToast({
                        icon: 'none',
                        title: '用户账号或密码不正确',
                    });
                }
				*/
            },
            oauth(value) {
                uni.login({
                    provider: value,
                    success: (res) => {
                        uni.getUserInfo({
                            provider: value,
                            success: (infoRes) => {
                                /**
                                 * 实际开发中，获取用户信息后，需要将信息上报至服务端。
                                 * 服务端可以用 userInfo.openId 作为用户的唯一标识新增或绑定用户信息。
                                 */
                                //this.toMain(infoRes.userInfo.nickName);
								this.my_login()
                            }
                        });
                    },
                    fail: (err) => {
                        console.error('授权登录失败：' + JSON.stringify(err));
                    }
                });
            },
			my_login() {
			  var openid = wx.getStorageSync('openid');
			  var username = wx.getStorageSync('username') ? wx.getStorageSync('username') : '';
			  var user_phone = wx.getStorageSync('user_phone') ? wx.getStorageSync('user_phone') : '';
			  var user_name = wx.getStorageSync('user_name') ? wx.getStorageSync('user_name') : '';
			  var userInfo = wx.getStorageSync('userInfo');
			  var url = this.bypassword?weburl + '/api/web/user/login/user_login':weburl + '/api/web/user/login/user_xcx_login'
			  var username = this.account
			  var vcode = this.vcode
			  var password = this.password
			  var md5password = md5(this.password,md5_key,true)
			  var bypassword = this.bypassword
			  var login_type = bypassword?1:0
			  wx.request({
			    url: url,
			    method: 'POST',
			    data: {
			      username: username,
				  password:	password,
				  md5password:md5password,
			      wx_nickname: userInfo.nickName,
			      wx_headimg: userInfo.avatarUrl,
			      user_phone: user_phone,
			      user_name: user_name,
			      login_type: login_type,
				  smscode:vcode,
			      type: 8,
			      shop_type: shop_type
			    },
			    header: {
			      'Content-Type': 'application/x-www-form-urlencoded',
			      'Accept': 'application/json'
			    },
			    success: function (res) {
				  if(res.data.status == 'y'){
					  /*
					  uni.showToast({
					  	 icon: 'none',
					  	 title:'登录成功'+res.data.status,
					  });
					  */
					  uni.reLaunch({
					      url: '../hall/hall',
					  });
					  console.log('app login 用户基本信息:', res.data.result);
					  uni.setStorageSync('username', res.data.result['username']);
					  uni.setStorageSync('m_id', res.data.result['m_id']);
					  uni.setStorageSync('user_phone', res.data.result['user_phone']);
					  uni.setStorageSync('user_name', res.data.result['user_name']);
					  uni.setStorageSync('user_gender', res.data.result['user_gender']);
					  uni.setStorageSync('user_type', res.data.result['user_type']);
					    //uni.navigateBack();
					   //this.toMain(this.account);
					   
				  }else{
					   uni.showToast({
					       icon: 'none',
					       title: res.data.info?res.data.info:'用户账号或密码不正确',
					   });
				  }
			    }
			  });
			},
            toMain(userName) {
                this.login(userName);
                if (this.forcedLogin) {
                    uni.reLaunch({
                        url: '../main/main',
                    });
                } else {
                    uni.navigateBack();
                }

            },
			bypasswd() {
			    this.bypassword = !this.bypassword
			},
			getcode() {
				var that = this
				var shop_type = this.shop_type ;
				var phoneNo = this.account ;
				console.log('get code phone no:',phoneNo);
				if (!util.checkPhoneNumber(phoneNo)) {
				    uni.showToast({
				        icon: 'none',
				        title: '手机号格式错误'
				    });
				    return;
				}
			    wx.request({
			          url: weburl + '/api/web/user/login/login_sms_send',
			          method: 'POST',
			          data: { 
			            phoneNo: phoneNo,
			            shop_type:shop_type,
			          },
			          header: {
			            'Content-Type': 'application/x-www-form-urlencoded',
			            'Accept': 'application/json'
			          },
			          success: function (res) {
			            console.log(res.data);
						if(res.data.status=='y'){
							that.ret_info = res.data.data
							uni.showToast({
								 icon: 'none',
								 title:'验证码已发送',
								 duration:1500,
							});
						}else{
							uni.showToast({
								 icon: 'none',
								 title:'验证码获取失败'+res.data.info,
								  duration:2000,
							});
						}
						 
			          }
			        })
			},
			 
        },
        onReady() {
            this.initPosition();
            this.initProvider();
        }
    }
